body {
	
  margin: 0;
  padding: 0;
  font-family: verdana, sans-serif;
}

.article {
	text-decoration: none;
}

.source {
	color: grey;
	
}

.time {
	border: 1px solid grey;
	padding: 3px;
	color: grey;
	border-radius: 3px;
	font-size: .8rem;
}

hr {
	background-color: lightgrey;
}


.topbar{
	margin: 10px;
}

.container{
	display: -ms-flexbox;
	display: flex;
	height: 95vh;
	border-top: 1px solid lightgrey;

}


.left{
	min-width: 200px;
	padding: 10px;
	-ms-flex: 1 1;
	    flex: 1 1;
	border-right: 1px solid lightgrey;
}

.news{
	
	padding: 10px;
	-ms-flex: 3 1;
	    flex: 3 1;
	overflow-y: scroll;

}

.right{
	
	padding: 10px;
	-ms-flex: 1 1;
	    flex: 1 1;
	border-left: 1px solid lightgrey;
	
}



.newsItem{
	border-bottom: 1px solid lightgrey;
	display: -ms-flexbox;
	display: flex;
}



.newsDate{
	width: 130px;
	text-align: right;
	margin-right: 0px;
	padding-right: 0px;
	margin-top: 10px;
}


